<template>
  <div class="container">
    <div class="product__card">
      <img :src="getIce.img" alt="" />
      <div class="product__info">
        <h2>{{ getIce.title }}</h2>
        <p>{{ getIce.text }}</p>
        <div class="product__item">
          <a class="product__price">$19.55</a>
          <a class="product__price2">$22.55</a>
        </div>
        <router-link to="/product">
          <a href="#!" class="product__link">Buy Now</a>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
import bd from "@/bd";
export default {
  data() {
    return {
      ices: bd,
      currentId: null,
    };
  },

  created() {
    this.currentId = this.$route.params.id;
  },
  computed: {
    getIce() {
      return this.ices.find((ice) => this.currentId == ice.id);
    },
  },
};
</script>
<style lang=""></style>
